import React, {useEffect, useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import {Button} from "@material-ui/core";
import "./project.css";
import getAverageColor from "get-average-color";

export default function TransitionsModal({imgSanity,lang}) {




    const useStyles = makeStyles((theme) => ({
        modal: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        paper: {
            backgroundColor: theme.palette.background.paper,
            border: '2px solid #000',
            boxShadow: theme.shadows[5],
            padding: "2px",

        },

    }));


    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };


    const [col,setCol] = useState(undefined);
    useEffect(()=> {
        getAverageColor(imgSanity).then(rgb => setCol(rgb))
    },[imgSanity]);

    const sty = [];
    if (col===undefined){return false }
    else {
        sty.push(`rgb(${col.r}, ${col.g}, ${col.b},0.60)`)
    }

    return (
        <div>

            <Button variant="outlined" onClick={handleOpen} className="btnModal" >

                {lang==="ru" ? `Показать на весь екран` : 'Show full screen image'}
            </Button>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
                style={{backgroundColor: sty}}
            >

                <Fade in={open}>
                    <div className={classes.paper}>
                        <img src={imgSanity}   className="imgModal"/>
                    </div>
                </Fade>
            </Modal>
        </div>
    );
}
